:root {
  --header-bg: #06297A;
  --hdr-txt-color: #ACB67C; 
  --txt-color: #A97283;
  --footer-bg: #A30031;
  --gradient: linear-gradient(#faf0f4, #f891b7);
  --dark-accent: #4b8a9d;
  --medium-accent: #d7aebc;
  --body-bg: #E8EBF4;
}


* {
  font-family: "Lucida Sans Unicode", "Lucida Grande", sans-serif;
  margin: 0;
  padding: 0;  
}

body {
  background-color: var(--body-bg);
}

header {
  position: fixed;
  display: flex;
  justify-content: space-between;
  background-color: var(--header-bg);
  color: var(--hdr-txt-color);
  width: 100%;
  z-index: 20;
}

header h1 {
  font-size: 250%;
  padding: 15px;
}

header > h1 > span {
  letter-spacing: -.18em;
}

main {
  padding: 5px;
  text-align: center;
}

a {
  color: var(--hdr-txt-color);
}

h2 {
  font-size: 180%;
  margin: 25px;
  padding-bottom: 12px;
  border-bottom: 3px;
  border-bottom-color: #B9C4DF;
  border-bottom-style: solid;
  color: #123585;
}

nav > ul {
  display: flex;
  justify-content: space-evenly;
  list-style-type: none;
  padding-top: 40px;
}

nav > ul > li {
  padding: 10px;
}

nav > ul > li > a:hover {
  color: #B2D507;
}

main > section {
  margin: 10px;
  border-color: #B9C4DF;
  border-width: 3px;
  border-style: solid;
  border-radius: 25px;
  padding: 12px;
}

#bio-pic {
  height: 450px;
  align-content: center;
  margin-top:80px;
}

p {
  font-size: 10pt;
  text-align: left;
  margin:30px;
}

.projects-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.projects-list > li:first-child {
  flex: 1 0 100%;
}

#projects-list:not(:first-child) {
  flex: 1 0 50%;
}

.pj-card {
  position: relative;
}

.pj-card > a > img {
  object-fit: cover;
  border: 3px;
  padding: 20px;
  margin: 12px;
  height: 250px;
}

.pj-card:first-child > a > img {
  object-fit: cover;
  border: 3px;
  padding: 20px;
  margin: 12px;
  height: 350px;
  max-width: 100%;
}

.pj-card > a > img:hover {
  background: #4b8a9d;
}

/* Bottom left text */
.card-text {
  position: absolute;
  bottom: 10px;
  left: 10px;
  background-color: black;
  color: white;
  padding-left: 20px;
  padding-right: 20px;
}

label {
  display: inline-block;
  width: 150px;
  text-align: right;
}

#email p {
  text-align: center;
}
footer {
  background-color: var(--footer-bg);
  color: var(--txt-color);
  width: 100%;
  height: 50px;
  line-height: 40px;
  text-align: center;
}

@media screen and (max-width: 768px) {
  header {
      flex-direction: column;
      justify-content: flex-start;
  }

  nav > ul {
      flex-direction: column;
      flex-wrap: wrap;
      padding: 5px;
  }

  section > img {
      margin-top:170px;
  }

  .pj-card:first-child > a > img {
      height: 250px;
  }

  .pj-card > a > img {
      height: 200px;
  }
}